import React, {useState} from 'react';
import loader from './Loader';

const Contact = (props) => {
  const {city, state, zip} = props.data.address;
  const {name, phone, email, contactMessage} = props.data;

  const [contactForm, setContactForm] = useState({
    contactName: '',
    contactEmail: '',
    contactSubject: '',
    contactMessage: '',
  });
  const [showLoader, setLoader]=useState(false);

  const handleChange = (event) => {
    setContactForm({
      ...contactForm,
      [event.target.id]: event.target.value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(contactForm);

    const serviceId = 'default_service';
    const templateId = 'template_aKSXTcJe';
    const template_params = {
      name: contactForm.contactName,
      Subject: contactForm.contactSubject,
      email: contactForm.contactEmail,
      message: contactForm.contactMessage,
    };
    setLoader(true);
    window.emailjs
        .send(serviceId, templateId, template_params)
        .then((res) => {
          console.log('Email successfully sent!');
          setLoader(false);
          resetForm();
        })
    // Handle errors here however you like, or use a React error boundary
        .catch((err) => {
          console.error(
              'Oh well, you failed. Here some thoughts on the error that occured:',
              err,
          );
          setLoader(false);
        },
        );
  };

  const resetForm = () =>
    setContactForm({
      contactName: '',
      contactEmail: '',
      contactSubject: '',
      contactMessage: '',
    });

  return (
    <section id="contact">
      <div className="row section-head">
        <div className="two columns header-col">
          <h1>
            <span>Get In Touch.</span>
          </h1>
        </div>
        <div className="ten columns">
          <p className="lead">{contactMessage}</p>
          <br />
        </div>
      </div>

      <div className="row">
        <div className="eight columns">
          <form
            onSubmit={(event) => handleSubmit(event)}
            id="contactForm"
            name="contactForm"
          >
            <fieldset>
              <div>
                <label htmlFor="contactName">
                  Name <span className="required">*</span>
                </label>
                <input
                  type="text"
                  value={contactForm.name}
                  size="35"
                  id="contactName"
                  name="contactName"
                  onChange={(event) => handleChange(event)}
                />
              </div>

              <div>
                <label htmlFor="contactEmail">
                  Email <span className="required">*</span>
                </label>
                <input
                  type="text"
                  value={contactForm.email}
                  size="35"
                  id="contactEmail"
                  name="contactEmail"
                  onChange={(event) => handleChange(event)}
                />
              </div>

              <div>
                <label htmlFor="contactSubject">Subject</label>
                <input
                  type="text"
                  value={contactForm.subject}
                  size="35"
                  id="contactSubject"
                  name="contactSubject"
                  onChange={(event) => handleChange(event)}
                />
              </div>

              <div>
                <label htmlFor="contactMessage">
                  Message <span className="required">*</span>
                </label>
                <textarea
                  value={contactForm.message}
                  cols="50"
                  rows="15"
                  id="contactMessage"
                  name="contactMessage"
                  onChange={(event) => handleChange(event)}
                />
              </div>

              <div>
				  {showLoader ?
					  <span id="image-loader">
				    <img alt="" src="images/loader.gif"/>
				   </span>:
					  <button className="submit">Submit</button>
				  }
              </div>
            </fieldset>
          </form>

          <div id="message-warning"> Error boy</div>
          <div id="message-success">
            <i className="fa fa-check" />
            Your message was sent, thank you!
            <br />
          </div>
        </div>

        <aside className="four columns footer-widgets">
          <div className="widget widget_contact">
            <h4>Address and Phone</h4>
            <p className="address">
              {name}
              <br />
              {city} <br />
              {state}, {zip}
              <br />
              <span>{phone}</span>
              <br />
              <span>
                <a href="mailto:d.selal@hotmail.com">{email}</a>
              </span>
            </p>
          </div>
        </aside>
      </div>
    </section>
  );
};

export default Contact;



